body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
outline: none;
}

.toggle-button__line {
    width: 30px;
    height: 2px;
    background: white;
}
.toolbar {
    position: fixed;
    z-index: 99;
    top: 0px;
    top: 0;
    left: 0;
    width: 100%;
    background: rgba(25, 46, 94, 0.952);
    height: 7%;
}

.toolbar__navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar__logo {
    margin-left: 1rem}

.toolbar__logo a {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
}

.spacer {
    flex: 1 1;
}

.toolbar_navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar_navigation-items li {
    padding: 0 3rem;
    border-left: 1px solid rgb(131, 128, 128);
}

.toolbar_navigation-items a {
    color: white;
    text-decoration: none;
    font-size: 20px;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
    color: rgb(58, 36, 250);
}

@media (max-width: 360px) {
    .homeIcon {
        display: none;
    }
}

@media (max-width: 768px) {
    .toolbar_navigation-items {
        display: none;
    }
}

@media (min-width: 769px) {
    .toolbar__toggle-button {
        display: none;
    }
    .toolbar__logo {
        margin-left: 0;
    }
}
.side-drawer {
    height: 100%;
    background:rgba(4, 74, 107, 0.89);
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 60%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0;
}


.side-drawer a {
    color: rgb(248, 248, 250);
    text-decoration: none;
    font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: rgb(30, 30, 150);
}

@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(2, 20, 70, 0.3);
