}
.lunaAboutUs {
    width: 100%;
    height: auto;
    margin-top: -80px;
    position: relative;
    filter: blur(3px);
  -webkit-filter: blur(3px);
}

@-webkit-keyframes zoominoutsinglefeatured {
    0% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    50% {
        -webkit-transform: scale(1.2,1.2);
                transform: scale(1.2,1.2);
    }
    100% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
}

@keyframes zoominoutsinglefeatured {
    0% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    50% {
        -webkit-transform: scale(1.2,1.2);
                transform: scale(1.2,1.2);
    }
    100% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
}

.lunaAboutUs {
	-webkit-animation: zoominoutsinglefeatured 15s infinite ;
	        animation: zoominoutsinglefeatured 15s infinite ;
}

